@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap);
* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
  overflow-x: hidden;
}

.App {
  height: 100%;
  width: 100vw;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  position: relative;
}

#footer_div {
  display: flex;
  width: 100%;
  background-color: #091531;
  position: absolute;
  bottom: 0;
  color: #fff;
  flex-direction: column;
  padding: 14px;
  align-items: center;
  justify-content: center;
}
#footer_div p {
  font-family: "avenir";
}

@font-face {
  font-family: "sf";
  src: url(/static/media/sanFrancisco.520fc56b.otf);
}
@font-face {
  font-family: "sf_script";
  src: url(/static/media/sanFranciscoScript.fcae4bc3.otf);
}
@font-face {
  font-family: "avenir";
  src: url(/static/media/avenir.b5ed9330.otf);
}
@font-face {
  font-family: "avenir_bold";
  src: url(/static/media/avenir_bold.91d8ff8a.otf);
}

.app {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
}

#JJ {
  height: 150px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
#JJ #joao {
  position: absolute;
  top: 15px;
  font-family: "sf";
  font-size: 73px;
  color: #0e0838;
}

#JJ #jessica {
  position: absolute;
  top: 43px;
  font-family: "sf_script";
  font-size: 97px;
  color: #eba4c9;
}

#data {
  display: flex;
  flex-direction: row;
  width: 100%;
  /* height: 6%; */
  justify-content: center;
  align-items: center;
}
#data hr {
  width: 45%;
  /* margin: 0 20px 0 20px; */
  border: 1px solid #eba4c9;
}
#data span {
  min-width: 170px;
  margin: 0 15px;
  color: #eba4c9;
  font-family: "avenir";
  text-align: center;
}

.app nav {
  /* height: 24%; */
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin-top: 20px;
}
#end_menu {
  display: none;
}
@media screen and (max-width: 760px) {
  .app nav {
    display: flex;
    flex-direction: column;
    background: rgb(235, 166, 201, 0.1);
  }
  #end_menu {
    display: block;
    width: 90% !important;
    opacity: 0.3 !important;
    margin: 10px 0;
  }
}

.app a {
  text-decoration: none;
  color: #0e0838;
  font-family: "Montserrat";
  border: 2px solid transparent;
  margin: 10px;
}
.app a:hover {
  border-bottom: 2px solid #0e0838;
  transition: 0.2s;
}
.app a:not(:hover) {
  transition: 0.2s;
}

li {
  list-style-type: none;
  font-family: "avenir";
  cursor: pointer;
  font-size: 15px;
}

#confirme_presenca {
  font-family: "avenir_bold";
  color: #0e0838;
}

@media screen and (max-width: 450px) {
  #JJ {
    /* height: 70%; */
  }
}

.container_home{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
#header_container {
  width: 100%;
  text-align: center;
}
#subtracao {
  width: 80%;
}

@media screen and (max-width: 600px) {
  #subtracao {
    display: none;
  }
}

@font-face {
  font-family: "avenir";
  src: url(/static/media/avenir.b5ed9330.otf);
}
@font-face {
  font-family: "avenir_bold";
