}

.div_cards {
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media screen and (max-width: 600px) {
  .div_cards {
    height: 100%;
  }
}
#dia_sim {
  font-family: "avenir_bold";
}
.div_cards p {
  padding: 10px 10%;
  font-family: "avenir";
}

#container_cards {
  height: 100%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
  justify-content: center;
  /* padding: 2%; */
}

@media screen and (max-width: 600px) {
  #container_cards {
    display: flex;
    flex-direction: column;
  }
}

#item_cards {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  height: 100%;
  padding: 20px 0;
}

#card_1 {
  display: flex;
  flex-direction: row;

  background: #efcfdf;
  height: 47%;
  width: 80%;
  margin-left: 10%;
  border-radius: 20px;
  box-shadow: 0px 3px 6px rgb(214, 213, 213);
  padding: 20px;
}
@media screen and (max-width: 600px) {
  #card_1 {
    margin: 10px 0;
  }
  #item_cards {
    width: 100%;
  }
}
#icon_div {
  width: 35%;
  display: flex;
  align-items: center;
  justify-content: center;
}
#icon_div img {
  width: 80px;
  height: 160px;
  max-height: 80%;
  max-width: 60%;
}

@media screen and (max-width: 600px) {
  #icon_div img {
    max-height: 50%;
  }
}

#info {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  text-align: left;
  justify-content: space-evenly;
}
/* #info span{
    padding: 4px;
} */
#cerimony {
  color: #0e0838;
  opacity: 0.6;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16px;
}

#time {
  color: #0e0838;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 20px;
}
#place {
  color: #0e0838;
  font-weight: bold;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 30px;
}

#adress {
  color: #0e0838;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 23px;
}

@media screen and (max-width: 600px) {
  #place {
    font-size: 23px;
  }
  #adress {
    font-size: 16px;
  }
}
#btns_info {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
#btns_info button {
  width: 49%;
  padding: 10px 0;
  border-radius: 25px;
  background: #fff;
  border: none;
}

@media screen and (max-width: 600px) {
  #btns_info {
    flex-direction: column;
  }
  #btns_info button {
    width: 100%;
    margin: 5px 0;
  }
}
#btns_info button:hover {
  cursor: pointer;
}
#waze_link {
  color: #9cc4d1;
  text-decoration: none;
  font-size: large;
  font-family: "avenir";
}
#maps_link {
  color: #449c72;
  text-decoration: none;
  font-size: large;
  font-family: "avenir";
}
#card_2 {
  display: flex;
  background: #fff;
  flex-direction: row;
  padding: 10px;
  height: 30%;
  width: 80%;
  margin-right: 10%;
  border-radius: 20px;
  box-shadow: 0px 3px 6px rgb(214, 213, 213);
  font-family: "avenir";
