}
@media screen and (max-width: 600px) {
  #card_2 {
    margin: 10px 0;
    height: 270px;
  }
}
#info_suit {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  text-align: left;
  justify-content: space-evenly;
}
#suit {
  font-family: "avenir_bold";
  color: #0b0e35;
}
#suit_tips {
  font-family: "avenir";
  font-size: 15px;
}

#uber {
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  padding: 5px;
}
#uber #title {
  font-size: 16px !important;
  font-family: "avenir_bold" !important;
}

#uber button {
  background-color: #333333;
  color: #fff;
  padding: 10px;
  border-radius: 15px;
  border: none;
  width: 100%;
  cursor: pointer;
}
#uber_request {
  font-size: 10px !important;
}
#uber a {
  text-decoration: none;
  color: #fff;
  font-family: "avenir_bold";
}
#camera_icon {
  height: 40% !important;
  width: 50% !important;
}
.camera_div {
  width: 50% !important;
  display: flex;
  align-items: center;
  justify-content: center;
}
#hashtag_title {
  font-size: 16px;
  font-family: "avenir_bold";
}
#filter_button {
  width: 100%;
  border: none;
  color: #fff;
  border-radius: 15px;
  background-color: #f5ccdf;
  padding: 10px;
  font-family: "avenir_bold";
  cursor: pointer;
}
@media screen and (max-width: 600px) {
  #camera_icon {
    height: 20% !important;
    width: 80px !important;
  }
}

