  border-radius: 30px;
  background-color: #091233;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
#confirm_div button:hover {
  transform: translateY(-5px);
  transition: 0.3s ease-in-out;
}
#confirm_div button:not(:hover) {
  transition: 0.3s;
}

@media screen and (max-width: 600px) {
  #confirm_div {
